import axios from "axios";
import store from "../../store/index";
export const sliderController = {
    getItems: async function () {
        var mainUrl = store.state.mainUrl;
        var data = [];
        await axios.get(mainUrl + '/slider').then((res) => {
            console.log(res.data.data);
            data = res.data.data
            store.commit("SET_SLIDER", data);

        });
        return data;
    },
    addSlider: async function (file, slider) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("title[en]", slider.title.en);
        formData.append("title[ar]", slider.title.ar);
        formData.append("title[tr]", slider.title.tr);
        formData.append("description[en]", slider.description.en);
        formData.append("description[ar]", slider.description.ar);
        formData.append("description[tr]", slider.description.tr);
        await axios.request({
            method: "POST",
            url: mainUrl + "/slider",
            data: formData,
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                if (res.data.state) {
                    state = true;
                    this.ShowAlert('secondary', 'Don Add Success ')
                    console.log(res.data);
                } else {
                    this.ShowAlert('red', res.data.error)
                }
            }
        }).catch(error => {
            this.ShowAlert('red', 'An error ' + error)
        });
        return state;
    },

    UpdateSlider: async function (file, slider) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("title[en]", slider.title.en);
        formData.append("title[ar]", slider.title.ar);
        formData.append("title[tr]", slider.title.tr);
        formData.append("description[en]", slider.description.en);
        formData.append("description[ar]", slider.description.ar);
        formData.append("description[tr]", slider.description.tr);
        await axios.request({
            method: "POST",
            url: mainUrl + "/slider/" + slider.id,
            data: formData,
            headers: this.configAxios(),
        }).then(res => {
            if (res.status == 200) {
                if (res.data.state) {
                    state = true;
                    this.ShowAlert('secondary', 'Don Update Success ')
                    console.log(res.data);
                } else {
                    this.ShowAlert('red', res.data.error)
                }
            }
        }).catch(error => {
            this.ShowAlert('red', 'An error ' + error)
        });
        return state;
    },
    deleteItem: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/slider/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ' + error);
            }
            );
        return state;

    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}